import type { CalendarAction, ClinicalRecommendation, Recommendations, RewardsStatus, SensorData } from "@/lib/types"

export type AgentName = "Sentry" | "Negotiator" | "Clinical" | "Rewards"

export interface AgentThoughtLog {
  id: string
  agent: AgentName
  label: string
  content: string
  createdAt: string
}

export interface AgentActionCard {
  id: string
  title: string
  context: string
  proposal: string
  metadata: Record<string, string>
  status: "pending" | "approved" | "ignored"
  actions: Array<{ label: string; variant?: "primary" | "ghost" | "destructive" }>
}

export interface FeedMessage {
  id: string
  agent: AgentName
  summary: string
  detail: string
  timestamp: string
  thoughts: AgentThoughtLog[]
  actionCard?: AgentActionCard
}

export interface BiometricMetric {
  id: string
  icon: string
  label: string
  value: number
  unit: string
  trend: number
}

